




























import Vue from 'vue';
import NumberPad from '@/components/Money/NumberPad.vue';
import FormItem from '@/components/Money/FormItem.vue';
import {Component} from 'vue-property-decorator';
import Tab from '@/components/Tab.vue';
import recordTypeList from '@/constants/recordTypeList';
import Tags1 from '@/components/Money/Tags1.vue';
import Income from '@/components/Money/Income.vue';

@Component({
  components: {Income, Tab, FormItem, Tags: Tags1, NumberPad},
})
export default class Money extends Vue {
  recordTypeList = recordTypeList;

  get recordList() {
    return this.$store.state.recordList;
  }

  record: RecordItem = {
    tags: {id: '', name: ''}, notes: '', type: '-', amount: 0, createdAt: new Date().toISOString()
  };

//  selectedTags=this.record.type;

  created() {
    this.$store.commit('fetchRecords');
  }

  onUpdateNotes(value: string) {
    this.record.notes = value;
  }

  saveRecord() {
    if (!this.record.tags) {
      alert('请选择一个标签哦~');

    } else {
      this.$store.commit('createRecord', this.record);
    }

  }
}
