




















import {Component, Prop} from 'vue-property-decorator';

import Vue from 'vue';


@Component({})

export default class Tags extends Vue {
  height = screen.availHeight;
  currentHeight: number = this.height - 260;
  selectedTag: Tag = {id: '', name: ''};

  mounted() {
    this.selectedTag = this.$store.state.tagList[0];
    this.$emit('update:value', this.selectedTag);
    (<HTMLElement>document.querySelector('.tags'))
        .style.paddingBottom = `${this.currentHeight}px`;} // eslint-disable-line Parsing error

  get tagList() {
    return this.$store.state.tagList;      //Ts里使用computed要用getter语法
  }

  @Prop(String) tagType!: string;

  create() {
    console.log(this.tagList);
  }

  chooseTag(tag: Tag) {
    this.selectedTag = tag;
    this.$emit('update:value', tag);
    return this.selectedTag;
  }

  created() {
    this.$store.commit('fetchTags');
  }


  editTags() {
    this.$router.push('/labels');
  }
}


