




























import {Component} from 'vue-property-decorator';
import Button from '@/components/Button.vue';
import {mixins} from 'vue-class-component';
import TagHelper from '@/mixins/TagHelper';
import '@/assets/icons/return.svg';
import createId from '@/lib/createId';


@Component({
  components: {Button}
})
export default class Labels extends mixins(TagHelper) {

  get tagList() {                  //Ts里使用computed要用getter语法
    return this.$store.state.tagList;
  }

  get extraList() {
    console.log(this.$store.state.extraList)
    return this.$store.state.extraList;

  }

  get currentTag() {
    return this.$store.state.currentTag;
  }

  remove(tag: Tag) {
    if (tag) {
      this.$store.commit('removeTag', tag.id);
    }
    this.extraList.push({id:tag.id, name:tag.name});
    this.$store.commit('saveTags')
  }
add(tag: Tag) {
  if (tag) {
    const id = createId().toString();
    this.tagList.push({id, name: tag.name});
    let index = -1;
    if (this.extraList.length>=1){
      for (let i = 0; i < this.extraList.length; i++) {
        if (this.extraList[i].id === tag.id) {
          index = i;
          break;
        }
      }
        if (index >= 0) {
          this.extraList.splice(index, 1);
      }
    }

    this.$store.commit('saveTags')}
}
  returnPage() {
    this.$router.go(-1);
  }

}
