























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import recordTypeList from '@/constants/recordTypeList';
import Tab from '@/components/Tab.vue';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';
import day from 'dayjs';
import FormItem from '@/components/Money/FormItem.vue';

@Component({
  components: {FormItem, Tab, Chart},
})

export default class Statistics extends Vue {
  recordTypeList = recordTypeList;
  type = '-';
  date = new Date().toISOString();

  x(isoString: string) {

    return dayjs(isoString).format('YYYY-MM-DD');
  }

  mounted() {
    const div = (this.$refs.chartWrapper as HTMLDivElement);

    // div.scrollLeft = div.scrollWidth;

  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {

    const {recordList} = this;

    const newList = clone(recordList)

        .filter(r => r.type === this.type)
        .sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());

    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);
    });
    return result;
  }

  get keyValueList() {
    const array = [];
    for (let i = 0; i <= 29; i++) {
      // this.recordList = [{date:7.3, value:100}, {date:7.2, value:200}]
      const dateString = day(this.date)
          .subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupedList, {
        title: dateString
      });
      array.push({
        id: 30 - i, key: dateString, value: found ? found.total : 0
      });
    }
    array.sort((a, b) => {
      if (a.key > b.key) {
        return 1;
      } else if (a.key === b.key) {
        return 0;
      } else {
        return -1;
      }
    });

    return array;
  }

  get rank(){
    const list: {     id: number;     key: string;     value: number}[]=[]
    clone(this.keyValueList as unknown  as {     id: number;     key: string;     value: number  }[]).map(t =>{
     if (t.value!=0){ list.push(t)}// eslint-disable-line

    })


    return  list.sort((a,b)=>(b.value-a.value))


  }

  get chartOptions() {
    const value = this.keyValueList.map(item => item.value);
    const date = this.keyValueList.map(item => item.key);

    return {
      grid: {
        left: 0,
        right: 0,
      },
      xAxis: {
        type: 'category',
        data: date,
        axisTick: {alignWithLabel: true},
        axisLine: {lineStyle: {color: '#666'}},

      },
      yAxis: {
        type: 'value',
        show: false,
        axisPointer: {
          snap: true
        }
      },
      series: [
        {
          symbol: 'circle',
          symbolSize: 6,
          itemStyle: {borderWidth: 1, color: '#666', borderColor: '#666'},
          // lineStyle: {width: 10},

          data: value,
          type: 'line'
        }],
      tooltip: {
        show: true, triggerOn: 'click',
        position: 'top',

      },

    };
  }}



