import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/lib/clone';
import createId from '@/lib/createId';


Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        recordList: [],
        tagList: [
            {id:createId().toString(),name:'餐饮'},
            {id:createId().toString(),name:'娱乐'},
            {id:createId().toString(),name:'购物'},
            {id:createId().toString(),name:'交通'},
            {id:createId().toString(),name:'娱乐'},
            {id:createId().toString(),name:'宠物'},
            {id:createId().toString(),name:'维修'},
            {id:createId().toString(),name:'居家'},
            {id:createId().toString(),name:'旅行'},
            {id:createId().toString(),name:'通讯'},
            {id:createId().toString(),name:'还款'},
        ],
        extraList: [
            {id:createId().toString(),name:'运动'},
            {id:createId().toString(),name:'数码'},
            {id:createId().toString(),name:'教育'},
            {id:createId().toString(),name:'美容'},
            {id:createId().toString(),name:'借出'},
            {id:createId().toString(),name:'捐赠'},
            {id:createId().toString(),name:'健身'},
        ],
        createTagError:null,
        currentTag: undefined
    } as RootState,
    mutations: {
        setCurrentTag(state, id: string) {
            state.currentTag = state.tagList.filter(t => t.id === id)[0];
        },
        updateTag(state, payload: { id: string; name: string }) {
            const {id, name} = payload;
            const idList = state.tagList.map(item => item.id);
            if (idList.indexOf(id) >= 0) {
                const names = state.tagList.map(item => item.name);
                if (names.indexOf(name) >= 0) {
                    window.alert('标签名重复了');
                } else {
                    const tag = state.tagList.filter(item => item.id === id)[0];
                    tag.name = name;
                    store.commit('saveTags');
                }
            }
        },
        removeTag(state, id: string) {
            let index = -1;
            for (let i = 0; i < state.tagList.length; i++) {
                if (state.tagList[i].id === id) {
                    index = i;
                    break;
                }
            }
            if ( state.tagList.length>1){
                if (index >= 0) {
                    state.tagList.splice(index, 1);
                    store.commit('saveTags');

                } else {
                    window.alert('删除失败');
                }
            }else window.alert("只剩下一个标签喽，去添加些新的标签吧~");


        },
        fetchRecords(state) {
            state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[];

            },
        createRecord(state, record) {
            const record2: RecordItem = clone(record);
            record2.createdAt = record2.createdAt || new Date().toISOString();
            state.recordList.push(record2);
            store.commit('saveRecords');
        },
        saveRecords(state) {
            window.localStorage.setItem('recordList',
                JSON.stringify(state.recordList));
        },
        fetchTags(state) {
            if (localStorage.getItem("tagList") === null){
                window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
            }else{   state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
                state.extraList = JSON.parse(window.localStorage.getItem('extraList') || '[]');}

           /* if (!state.tagList || state.tagList.length===0){
                store.commit('createTag','餐饮');
                store.commit('createTag','购物');
                store.commit('createTag','交通');
                store.commit('createTag','医疗');
                store.commit('createTag','娱乐');
                store.commit('createTag','旅行');
                store.commit('createTag','维修');
                store.commit('createTag','还款');
                store.commit('createTag','追星');
                store.commit('createTag','通讯');
                store.commit('createTag','居家');
                store.commit('createTag','宠物');
            }*/
           },

        createTag(state, name: string) {
            state.createTagError = null;
            const names = state.tagList.map(item => item.name);
            if (names.indexOf(name) >= 0) {
                state.createTagError = new Error('tag name duplicated');
                return;
            }
            const id = createId().toString();
            state.tagList.push({id, name: name});
            store.commit('saveTags');
        },
        saveTags(state) {
            window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
            window.localStorage.setItem('extraList', JSON.stringify(state.extraList));
        },
    }
});

export default store;