














import Vue from 'vue';
import {Component,Prop} from 'vue-property-decorator';


@Component({})
export default class Income extends Vue {
  @Prop(String) tagType!: string;
  List: Tag[] = [
    {id: '1', name: '工资'},
    {id: '2', name: '红包'},
    {id: '3', name: '礼金'},
    {id: '4', name: '收款'},
    {id: '5', name: '借入'},
    {id: '6', name: '理财'},
    {id: '7', name: '其他'},

  ];
  selectedTag: Tag ={id: '1', name: '运动'}
  /*mounted(){
    this.selectedTag ={id: '1', name: '运动'}
    console.log(this.selectedTag===this.tag)
  }*/
  chooseTag(tag: Tag) {
    this.selectedTag=tag
    this.$emit('update:value',tag);
    return this.selectedTag
  }
}
